import React from 'react';
import styled, { keyframes } from 'styled-components';
import { Link } from 'react-scroll';
import { GITHUB } from '../images';
import { SECTIONS as NavLinks } from './../constants';
// components
import { Logo } from './common';

const Header = () => (
  <StyledHeader data-aos="slide-left">
    <GithubIcon src={GITHUB} />
    <NavList>
      {NavLinks.map(({ section }, i) => (
        <LinkStyled
          key={i}
          to={section}
          spy={true}
          smooth={true}
          duration={500}
          activeClass="active"
        >
          {section}
        </LinkStyled>
      ))}
    </NavList>
  </StyledHeader>
);

const LinkStyled = styled(Link)`
  color: white;
  cursor: pointer;
  width: 150px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: box-shadow 0.2s linear, color 0.1s linear;
  &.active {
    box-shadow: 0 -5px 0 0 ${({ theme }) => theme.color.primary} inset;
    color: ${({ theme }) => theme.color.primary};
  }
`;

const NavList = styled.ul`
  height: 100%;
  height: 50px;
  margin: 0 0;
  border: ${({ theme }) => theme.color.dark} 3px solid;
  display: flex;
`;

const StyledHeader = styled.div`
  background: ${({ theme }) => theme.color.dark};
  width: 100%;
  height: 50px;
  padding: 1%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  border-bottom: 2px solid ${({ theme }) => theme.color.dark};
`;

const wobble = keyframes`
  0% { transform: rotate(0deg) }
  25% { transform: rotate(-30deg) }
  50% { transform: rotate(30deg) }
  75% { transform: rotate(-30deg) }
  100% { transform: rotate(0deg) }
`;

const GithubIcon = styled(Logo)`
  &:hover {
    animation: ${wobble} 1s;
  }
`;

export default Header;
